import React from "react";
import { Table } from "react-bootstrap";
// 80-7) connect import 해야 사용 가능
// 104-3) import useSelector
// 104-4) import useDispatch
import { connect, useDispatch, useSelector } from 'react-redux';
import '../Detail.scss'

// 장바구니 페이지 
function Cart(props) {

    // 104-2) 훅 사용, 콜백 함수 추가, redux 에 있는 모든 state 가져오기
    let state = useSelector((state) => state);
    console.log("useSelector, state : %s", state);

    // 104-3) 훅 사용, 디스패치 함수 가져다 쓸수 있음. props 없이.
    let dispatch = useDispatch();

    return (
        <div>

            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>수량변경</th>
                        <th>가격</th>
                    </tr>
                </thead>
                <tbody>

                    {
                        // props.state.map((stuff1, idx) => {
                        state.reducer.map((stuff1, idx) => {
                            console.log("stuff1[%d]: %s", idx, stuff1);
                            // 가져온 값 화면에 세팅
                            return (
                                <tr key={idx}>
                                    <td>{stuff1.name}</td>
                                    <td>{stuff1.quan}</td>
                                    <td><button onClick={() => {
                                        // 수량증가 dispatch({type:'타입', payload: 보낼데이터})
                                        // 104-5) useDispatch() 훅으로 props 없이 그냥 가져다 쓸수 있음.
                                        // props.dispatch({ type: '수량증가', payload: { name: 'A' }, id: idx })
                                        dispatch({
                                            type: '수량증가', payload: stuff1
                                        })

                                    }}>+</button>

                                        <button onClick={() => {
                                            // 수량감소 
                                            // 104-5) useDispatch() 훅으로 props 없이 그냥 가져다 쓸수 있음.
                                            // props.dispatch({ type: '수량감소', id: idx })
                                            dispatch({
                                                type: '수량감소', payload: stuff1
                                            })

                                        }}>-</button></td>
                                    <td>{stuff1.price}</td>
                                </tr>
                            );

                        })
                    }

                </tbody>
            </Table>
            {
                props.alertSale === true
                    ?
                    <div className="alert-sold-out-yellow">
                        <p>지금 구매하시면 할인 20%</p>
                        <button onClick={() => {
                            // 104-5) useDispatch() 훅으로 props 없이 그냥 가져다 쓸수 있음.
                            // props.dispatch({ type: '닫기' })
                            dispatch({ type: '닫기' })
                        }}>닫기</button>
                    </div>
                    : null
            }


        </div >
    )
}

