import React, { useContext, useState } from "react";
import { useHistory } from "react-router-dom";
import { Link } from 'react-router-dom';
import { remainContext } from "./App";

function Product(props) {
    console.log('image: %s, %d', props.stuff.img, props.stuff.id)

    // 60-3) context 훅 불러오기
    let remainStuff = useContext(remainContext);

    // 105-1) 클릭시 상세 화면으로 넘어가기
    let history = useHistory();

    return (
        <div className='col-md-4' onClick={() => { history.push('/detail/' + props.stuff.id) }}>
            {/* <h3>{props.idx}</h3> */}
            {/*
             <Link to={"/detail/" + props.stuff.id}>
                <img src={props.stuff.img} width="100%" />
            </Link> 
            */}

            <img src={props.stuff.img} width="100%" />
            <h4>{props.stuff.title}</h4>
            <p>{props.stuff.content}</p>
            <p>{props.stuff.price}</p>
            {remainStuff}
        </div>
    );
}

export default Product;