// 90-2) 기본 state 
let defaultState =
  [
    // { id: 0, name: 'TOTORO 인형 0', quan: 1, price: 10000 },
    // { id: 1, name: 'TOTORO 인형 1', quan: 2, price: 12000 },
    // { id: 2, name: 'TOTORO 인형 2', quan: 3, price: 11000 },
    // { id: 3, name: 'TOTORO 인형 3', quan: 4, price: 9000 }
  ];

// 90-1) redux 수정방법 미리 정의해 놓고, 정의에 맞는 수정하도록 한다. data 초기값 '='으로 설정.
function reducer(state = defaultState, actions) {
  // 101-1) 장바구니에 추가하기

  let copy = [...state]; // deep copy

  switch (actions.type) {
    case '항목추가':
      console.log("reducer:: actions.payload.id = %d", actions.payload.id);
      let idx = actions.payload.id;
      console.log("reducer:: idx = %d", idx);

      let found = false;
      if (copy.length > 0) {
        for (let i = 0; i < copy.length; i++) {
          if (actions.payload.id === copy[i].id) {
            // id 가 같으면 수량만 +1 
            copy[i].quan++;
            found = true;
            break;
          }
        }

        // 같은 id를 못했을때는 추가하기 
        if (found === false) {
          copy.push(actions.payload);
        }
      }
      else {
        copy.push(actions.payload);
      }
      return copy;
    case '수량증가':
      copy[actions.payload.id].quan++;
      return copy;
    case '수량감소':
      let quan = copy[actions.payload.id].quan;
      if (quan >= 1) {
        copy[actions.payload.id].quan--;
      }
      return copy;
    default:
      // 기본 state 를 리턴한다.
      return state;
  }
}

// 100-1) 카트 팝업 표시 하기 , 숨기기 용
let alertAdmob = true;

function reducer2(state = alertAdmob, actions) {
  if (actions.type === '닫기') {
    // 100-4) state 변경 
    state = false;
    return state;
  } else {
    return state;
  }
}

// 90-3) state 에 저장, 실제 쓰는 곳에서 state를 props로 변경해야함.
// 100-2) reducer 여러개 설정하기
//let store = createStore(reducer);
let store = createStore(combineReducers({ reducer, reducer2 }));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      {/* 80-4) store 에 담아 놓음. */}
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

