/**  
 * 1) 배포 
 * 특정사이트로 연결할때 package.json 에 항목 추가 
 * "homepage": "http://codingapple.com/blog", 
 * 
 * 2) 터미널에서 빌드
 * npm run build
 * build/ 폴더 안에 index.html, css파일, js 파일을 모두 서버에 올린다.
 * 
*/

import React, { useEffect, useState, lazy, Suspense } from 'react';
// ajax 사용을 위한 라이브러리
import axios from 'axios';

import { Navbar, Container, Nav, NavDropdown, Button } from 'react-bootstrap';
// 라우팅 시작
import { Link, Route, Routes, Switch } from 'react-router-dom';

import './App.css';

import exportData from './data/data.js';
import Product from './Product.js';
import Cart from './cart/Cart.js';
// 107-1) Detail 컴포넌트가 필요해질때만 로딩하도록 하는 방법 lazy 콜백함수에 ES6 다이나믹 함수 문법  
// import Detail from './Detail.js';
// let Detail = lazy(() => { return import('./Detail.js') });
let Detail = lazy(() => import('./Detail.js'));





// 60-1) 같은 변수값을 공유할 범위 생성
export const remainContext = React.createContext();

function App() {

  // 1-1) 데이터 js 에서 가져오기
  let [stuff, stuffChange] = useState(exportData);

  // var stuffList = stuff.map((stuff, idx) =>
  //   <div className='col-md-4'>
  //     <h3>{idx}</h3>
  //     <img src={stuff.img} width="100%" />
  //     <h4>상품명 {stuff.title}</h4>
  //     <p>{stuff.content}</p>
  //     <p>{stuff.price}</p>
  //   </div>
  // );

  // 30-1) 로딩 alert 표시 여부 선언
  let [loadingView, loadingViewVisible] = useState(true);

  // 50-1) 재고표시 - Detail 컴포넌트 안에 데이터 바인딩하기
  let [stuffLeftCount, stuffLeftCountChange] = useState([10, 11, 12]);


  // 106-1) async 
  let [count, setCount] = useState(0);
  let [more, moreCount] = useState(100);


  // 106-3) useEffect() 훅 사용, state 가 변경되고 나서 실행되도록 한다.
  useEffect(() => {
    console.log("106-3) useEffect() 훅 실행")
    // 106-4) 로딩 될때 한번은 반드시 실행하게 되므로 조건 추가
    if (count != 0 && count < 3) {
      console.log("106-4) count 가 0이 아닐때 카운트 추가 실행")
      moreCount(more + 1)
    }
  }, [count]);


  return (
    <div className="App">
      {/* 상단 네비이션 */}
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">ZIBRI SHOP</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/cart">장바구니</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      {/* 라우터 하나만 보여주기 Switch path가 중복일때 맨 위에 하나만 보여주기 */}
      <Switch>
        <Route exact path="/">
          <div>
            {/* 대문 */}
            <div className='jumbotron'>
              <h1 className='h1'>지브리 박물관</h1>
              <p>
                This is a simple hero unit, a simple jumbotron-style component for calling
                extra attention to featured content or information.
              </p>
              <p>
                <Button variant="primary" onClick={() => {
                  console.log("106-2) 클릭 실행")

                  // 106-2) count +1 하기
                  setCount(count + 1);
                  // if (count < 3) {
                  //   // more 같이 +1 하기 
                  //   moreCount(more + 1);
                  // }
                }}>클릭</Button>
                <div>클릭수 {count}, {more}</div>
              </p>
            </div>

            {/* bootstrap 원조는 className 사용하는 대신 css 용량이 큼, react-bootstrap 은 component 를 사용 */}
            <div className="container">

              {/* 60-2) 설정 공유할 html을 범위로 감싸기 */}
              <remainContext.Provider value={stuffLeftCount}>
                <div className="row">

                  {/* {stuffList} 상단 map 에서 같이 씀 */}
                  {/* <Product stuff={stuff[0]} /> */}

                  {
                    stuff.map((one, idx) => {
                      console.log("stuffLeftCount: %d, one: %s ", stuffLeftCount[idx], one.id);
                      var count = stuffLeftCount[idx];
                      console.log("stuffLeftCount count: %d ", count);

                      // return <Link to={"/detail/" + one.id}>
                      //   <Product stuff={one} key={idx} />
                      // </Link>
                      return <Product stuff={one} key={idx} />
                    })
                  }
                </div>
              </remainContext.Provider>
              <button className="btn btn-primary" onClick={() => {
                // 20-1) javascript 로 쓸때 단, 호환성이 좋지 않음. 
                // fetch('http://codingapple1.github.io/shop/data2.json').then('성공')

                // 30-1) 로딩중이라는 ui 열기
                // {
                //   loadingView === true ?
                //     (<div className='loading-view'>
                //       <p>로딩중 입니다...</p>
                //     </div>)
                //     : null
                // };

                // 40-1) 서버에 데이터 보내기, header cookie, 등 설정 하는 방법 찾아서 해보기
                // axios.post('url', {id:'', pw:''});


                // 새로고침 없이 데이터 가져오기 
                axios.get('https://ssc73111.github.io/data/add_stuff.json')
                  .then((result) => {
                    console.log('경로설정대로 데이터 가져오기 성공! %s', result.data);

                    // 30-2) 로딩중이라는 ui 닫기
                    loadingViewVisible(false);

                    // 기존 리스트에 카피 후 추가 하기 
                    stuffChange([...stuff, ...result.data]); // [{},{},{}] 배열에 내려받은 데이터 배열 중 [] 제거 후 object 배열만 추가하기


                  })
                  .catch(() => {
                    console.log('데이터 가져오기 실패!');

                    // 30-2) 로딩중이라는 ui 닫기
                    loadingViewVisible(false);
                  });
              }}>더보기</button>
            </div>
          </div>
        </Route>
        <Route path="/detail/:id">
          {/* 60-2) 설정 공유할 html을 범위로 감싸기 */}
          <remainContext.Provider value={stuffLeftCount}>
            {/* 107-2) lazy 함수를 쓰려면 컴포넌트를 Suspense 로 묶어야 실행됨 */}
            <Suspense fallback={<div>로딩중입니다.</div>}>
              <Detail stuff={stuff} stuffLeftCount={stuffLeftCount} stuffLeftCountChange={stuffLeftCountChange} />
            </Suspense>
          </remainContext.Provider>
        </Route>

        {/* 80-1) props 없이 모든 컴포넌트가 state를 갖다 쓰기 가능 */}
        <Route path="/cart">
          <div>카트 카드 카트 카드 카트</div>
          <Cart />
        </Route>

        <Route path="/:id">
          <div>아무 문자 넣은 경로 일때</div>
        </Route>


      </Switch>
      {/* <Route path="/detail2" componont={}></Route> */}
    </div>
  );
}


export default App;
